/* home cards */
#card-grid .col-md-6.col-lg-3 {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1.25rem;
}

#card-grid .card {
  width: 100%;
}

#card-grid .picto-link {
  display: block;
  text-align: center;
}

#card-grid .card-footer {
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  border: 0;
}

#card-grid #migrate-card img {
  width: 50%;
}

#angular-logo {
  display: inherit;
  width: 8rem;
  margin: 0 auto;
}

