@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  #card-grid .picto-link {
    height: 12.5rem;
  }
}

#bill .card-img,
#shop .card-img,
#timer .card-img,
#unlock .card-img {
  padding: 1.25rem;
}

#bill .card-img img,
#shop .card-img img,
#timer .card-img img,
#unlock .card-img img {
  width: 50%;
}

#bill .card-img {
  background-color: #50be87;
}

#shop .card-img {
  background-color: #4bb4e6;
}

#timer .card-img {
  background-color: #ffb4e6;
}

#unlock .card-img {
  background-color: #ffd200;
}

@media (max-width: 479.98px) {
